/* jshint ignore:start */

define('github-galore/config/environment', ['ember'], function(Ember) {
  var prefix = 'github-galore';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (!runningTests) {
  require("github-galore/app")["default"].create({"name":"github-galore","version":"0.0.0+fe2f08f9"});
}

/* jshint ignore:end */
